"use client"

import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { GameEntriesTable } from "./game-entries-table"
import { Trophy } from "lucide-react"
import type { Game, GameEntry } from "@/lib/mock-data"

interface WinnerModalProps {
  game: (Game & { winners?: string[] }) | null
  entries: GameEntry[]
  open: boolean
  onClose: () => void
}

export function WinnerModal({ game, entries, open, onClose }: WinnerModalProps) {
  if (!game) return null

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl border-border bg-card text-card-foreground">
        <DialogHeader>
          <DialogTitle className="text-xl">{game.title} - Results</DialogTitle>
        </DialogHeader>
        <div className="space-y-6">
          <div className="flex flex-col items-center justify-center py-6">
            <Trophy className="mb-4 h-12 w-12 text-accent" />
            <p className="mb-2 text-sm text-muted-foreground">Winning Number</p>
            <p className="gold-shimmer text-6xl font-bold">{game.winningNumber}</p>
          </div>

          {game.winners && game.winners.length > 0 ? (
            <Card className="border-accent/30 bg-accent/10">
              <CardHeader className="pb-2">
                <CardTitle className="text-center text-accent"> Winners </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="flex flex-wrap justify-center gap-2">
                  {game.winners.map((winner, idx) => (
                    <span key={idx} className="rounded-full bg-accent/20 px-4 py-2 font-semibold text-accent">
                      {winner}
                    </span>
                  ))}
                </div>
              </CardContent>
            </Card>
          ) : (
            <p className="text-center text-muted-foreground">No winners for this game.</p>
          )}

          <Card className="border-border bg-secondary/30">
            <CardHeader>
              <CardTitle className="text-base">All Entries</CardTitle>
            </CardHeader>
            <CardContent>
              <GameEntriesTable entries={entries} winningNumber={game.winningNumber} />
            </CardContent>
          </Card>
        </div>
      </DialogContent>
    </Dialog>
  )
}
